/* add css module styles here (optional) */

._2xeIt {
  display: flex;
  justify-content: center;
  align-items: center;
  height: inherit;
  background: #f4faff;
  overflow: hidden;
}

._HgYJS {
  position: relative;
  width: 50%;
  background: #0588e4 url('assets/earth.png');
  background-size: cover;
  border-radius: 50%;
  box-shadow: inset 0 0 50px rgba(0, 0, 0, 0.85);
  justify-content: center;
  align-items: center;
  display: flex;
  transition: 0.5s;
  -webkit-animation: _3olKP 12s linear infinite;
          animation: _3olKP 12s linear infinite;
}

._HgYJS:active {
  transform: scale(5);
}

._HgYJS:active ._XO3E2 {
  transform: rotate(-90deg) scale(0.2) !important;
}

._HgYJS::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: url('assets/cloud.png');
  background-size: cover;
  border-radius: 50%;
  overflow: hidden;
  -webkit-animation: _3olKP 12s linear infinite;
          animation: _3olKP 12s linear infinite;
  z-index: 1;
}

@-webkit-keyframes _3olKP {
  0% {
    background-position: 0 0;
  }

  100% {
    background-position: calc(719px - 80%) 0;
  }
}

@keyframes _3olKP {
  0% {
    background-position: 0 0;
  }

  100% {
    background-position: calc(719px - 80%) 0;
  }
}

/* to make it responsive */
._HgYJS:after {
  content: '';
  display: block;
  padding-bottom: 100%;
}

._XO3E2 {
  width: 20%;
  background: center / contain no-repeat url('assets/plane.png');
  transform: rotate(-90deg);
  transition: 0.5s;
  pointer-events: none;
  z-index: 2;
}

/* to make plane responsive */
._XO3E2:after {
  content: '';
  display: block;
  padding-bottom: 100%;
}
